.chat-header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;

  background-color: blueviolet;

  padding: 10px 40px;

  display: flex;
  align-items: center;

  .back-arrow {
    width: 35px;
    height: 35px;
    cursor: pointer;
  }

  .header-text {
    display: block;
    width: 100%;

    font-size: 35px;
    font-weight: normal;
    margin-left: 15px;

    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.chat-grid {
  overflow-x: hidden;
  display: grid;

  grid-template-columns: 40px 1fr 1fr 1fr 1fr 1fr 40px;
  grid-template-rows: auto;

  row-gap: 10px;

  padding: 10px 0;

  .loader {
    grid-column: 1 / 8;
    height: 50px;
    padding: 15px 0;

    img {
      display: block;
      height: 100%;
      margin: 0 auto;
    }
  }

  .message {
    padding: 10px;
    position: relative;

    &:after {
      position: absolute;
      content: '';
      width: 0;
      height: 0;
      border-style: solid;
    }

    .sender {
      font-weight: bold;
    }

    .date {
      display: inline-block;
      float: right;
      padding: 0 0 0 7px;
      position: relative;
      bottom: -6px;
      color: yellow;
      font-size: 12px;
    }

    .content {
      display: inline-block;
      font-family: inherit;
      white-space: pre-line;
    }

    &.others {
      grid-column: 2 / 5;
      justify-self: flex-start;

      background-color: grey;
      border-radius: 0px 5px 5px 5px;

      &::after {
        border-width: 0px 10px 10px 0;
        border-color: transparent grey transparent transparent;
        top: 0;
        left: -10px;
      }
    }

    &.mine {
      grid-column: 4 / 7;
      justify-self: flex-end;

      border-radius: 5px 0px 5px 5px;
      background-color: green;

      &::after {
        border-width: 0px 0 10px 10px;
        border-color: transparent transparent transparent green;
        top: 0;
        right: -10px;
      }
    }
  }
}

@media screen and (max-width: $mobile_breakpoint) {
  .chat-header {
    padding: 10px 20px;
  }

  .chat-grid {
    grid-template-columns: 20px 1fr 1fr 1fr 1fr 1fr 20px;

    .message {
      &.others {
        grid-column: 2 / 6;
      }

      &.mine {
        grid-column: 3 / 7;
      }
    }
  }

  .message {
    font-size: 14px;
  }
}
